import { a as client } from "../../../../../../chunks/connection.js";
import "../../../../../../chunks/index.js";
const load = async ({ url, depends }) => {
  depends("database");
  let pageNumber = Number(url.searchParams.get("page")) || 1;
  let limit = Number(url.searchParams.get("limit") || 10);
  if (limit < 0)
    limit = 10;
  const where = {
    verified: false
  };
  const count = await client.tickets.count({
    where
  });
  if (pageNumber > Math.ceil(count / limit)) {
    pageNumber = Math.ceil(count / limit);
  }
  if (pageNumber < 1)
    pageNumber = 1;
  let tickets = await client.tickets.findMany({
    take: limit,
    include: {
      viewer: true
    },
    skip: (pageNumber - 1) * limit,
    where
  });
  console.log(tickets);
  return {
    tickets,
    pageNumber,
    limit,
    count,
    filter: ""
  };
};
const actions = {
  verify: async ({ request, locals }) => {
    try {
      const data = await request.formData();
      const ticketId = Number(data.get("ticketId"));
      if (!ticketId) {
        return {
          success: false
        };
      }
      const res = await client.tickets.update({
        where: {
          ticket_id: ticketId
        },
        data: {
          verified: true
        }
      });
      return {
        success: true
      };
    } catch (error) {
      console.log(error);
      return {
        success: false
      };
    }
  }
};
export {
  actions,
  load
};
